import React from 'react';
import './App.css';

/* eslint-disable no-eval, react/no-direct-mutation-state, no-self-assign */

const numbers = [
  { id: "zero", name: 0 },
  { id: "one", name: 1 },
  { id: "two", name: 2 },
  { id: "three", name: 3 },
  { id: "four", name: 4 },
  { id: "five", name: 5 },
  { id: "six", name: 6 },
  { id: "seven", name: 7 },
  { id: "eight", name: 8 },
  { id: "nine", name: 9 },
  { id: "add", name: "+" },
  { id: "subtract", name: "-" },
  { id: "multiply", name: "x" },
  { id: "divide", name: "/" },
  { id: "equals", name: "=" },
  { id: "decimal", name: "." },
  { id: "clear", name: "AC" }
]

class App extends React.Component{
  render(){
    return (
      <div className="App">
          <Calculator/>
      </div>
    );
  }
}

class Calculator extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      formula: "",
      output: "",
      result: "",
      showOperator: false,
      operator: "",
      showZero: true,
      status: "calculating"
    }
  }

  handleResult = () => {
    if (this.state.showOperator) this.setState({formula: this.state.formula.slice(0, -1)});
     else if (this.state.formula !== ""){
      this.setState({
        result: parseFloat(eval(this.state.formula).toFixed(4)),
        output: "",
        operator: "",
        status: "done"
      })
    }
  }
  handleClear = () => {
    this.setState (
      {
        result: "",
        operator: "",
        formula: "",
        showZero: true,
        output: "",
        status: "calculating"
      }
    )
  }
  handleOperator = operator => {
    if (this.state.status === "done"){
      this.setState (
        {
          formula: this.state.result += operator,
          output: "",
          operator,
          showOperator: true,
          result: "",
          status: "calculating"
        }
      )
    } else {
      if (this.state.operator !== "" && operator === "-"){
        this.setState({
          formula: this.state.formula += operator,
          output: "",
          operator: this.state.operator += operator,
          showOperator: true,
          result: ""
        })
        } else {
        this.setState (
          {
            formula: this.state.formula += operator,
            output: "",
            operator,
            showOperator: true,
            result: ""
          }
        )
      }
    }
    operator = operator;
  }
  switchOperator = operator => {
    if (this.state.operator.length > 1){
      this.setState (
        {
          formula: this.state.formula.slice(0, -2) + operator,
          operator
        }
      )
    } else {
      this.setState (
        {
          formula: this.state.formula.slice(0, -1) + operator,
          operator
        }
      )
    }
  }
  displayNumber = button => {
    if (this.state.status === "done"){
      this.handleClear();
      this.setState (
        { 
          output: this.state.output += button,
          formula: button,
          showOperator: false,
          operator: "",
          showZero: false,
          status: "calculating"
        }
      )
    } else if (this.state.output.length < 20){
      this.setState (
        { 
          output: this.state.output += button,
          formula: this.state.formula += button,
          showOperator: false,
          operator: "",
          showZero: false
        }
      )
    }
  }
  render() {
    let operator = "";
    return (
      <div>
        <div className="container">
        <div className="display-wrapper">
          <div className="display-container">
          <div className="input">
            {this.state.formula}{this.state.result !== "" ? "=" + this.state.result : null}
          </div>
          <div id="display">
          {this.state.showZero && 0}
          {this.state.showOperator ? operator : this.state.result !== "" ? this.state.result : this.state.output.length >= 20 ? "DIGIT LIMIT REACHED" : this.state.output}
          </div>
           </div>
        </div>
        
        {numbers.map(i => 
          <Button 
          displayChange={this.handleDisplay}
          handleResult={this.handleResult}
          handleClear={this.handleClear}
          handleOperator={this.handleOperator}
          handleNumber={this.displayNumber}
          switchOperator={this.switchOperator}
          output={this.state.output}
          operator={this.state.operator}
          key={i.id}
          id={i.id}
          name={i.name}/>
        )}  
      </div>
        <div className="author">
          <a href="https://www.freecodecamp.org/elnino97" rel="author">Designed and coded by Aidas K.</a>
        </div>
      </div>
    )
  }
}

class Button extends React.Component {
   handleClick = (e) => {
    let buttonName = e.target.name;
    let output = this.props.output;
    if (/[\d]/g.test(buttonName)){
      if (!/^0/g.test(output) && buttonName !== "0") {
        this.props.handleNumber(buttonName);
      } else if (output !== "") {
        this.props.handleNumber(buttonName);
      }
    }
    if (buttonName === ".") {
      if (this.props.output === ""){
        this.props.handleNumber("0.")
      } else if (this.props.output.indexOf(".") === -1){
        this.props.handleNumber(".")
      }
    }
    if (buttonName === "AC") {
      output = "";
      this.props.handleClear();
    }
    if (/[-+x*/]/g.test(buttonName) && this.props.operator !== buttonName) {
      if (this.props.operator === "") {
        this.props.handleOperator(buttonName.replace("x", "*"));
      }
      if (this.props.operator !== "") {
        if (/[+x*/]/g.test(this.props.operator) && buttonName === "-" && this.props.operator.length < 2){
          this.props.handleOperator(buttonName);
        } else if (this.props.operator.length > 1){
          this.props.switchOperator(buttonName.replace("x", "*"))
        } else {
          this.props.switchOperator(buttonName.replace("x", "*"))
        }
      } 
    }
    if (buttonName === "=") {
      this.props.handleResult();
    }
  }
  render(){
    return (
      <button id={this.props.id} name={this.props.name} onClick={this.handleClick}>
        {this.props.name}
      </button>
    )
  }
}



export default App;
